.header4 {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  height: 78px;
  padding: 5px 25px;
  background: $white;
  box-shadow: inset 0px -1px 0px $border;
  @include t {
    padding: 5px 20px; }
  @include m {
    height: 60px;
    padding: 0 20px; }
  &__burger {
    display: none;
    font-size: 0;
    @include d {
      display: inline-block;
      margin-right: 25px; }
    @include m {
      margin-right: 15px; }
    .icon {
      font-size: 32px;
      fill: $gray;
      @include m {
        font-size: 24px; } } }
  &__logo {
    margin-right: auto;
    font-size: 0;
    &_empty {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 80px;
      height: 78px;
      flex-shrink: 0;
      @include t {
        display: inline-block;
        width: auto;
        height: auto; } }
    &_blue {
      background: $blue;
      @include d {
        background: none; } } }
  &__logo &__pic {
    width: 112px;
    &_white {
      display: none; } }
  &__logo_empty &__pic {
    width: 32px;
    @include t {
      width: 112px; }
    &_white {
      @include d {
        display: none; } }
    &_black {
      @include d {
        display: inline-block; }
      @include t {
        display: none; } }
    &_big {
      display: none;
      @include d {
        display: none; } }
    &_big.header4__pic_black {
      @include t {
        display: inline-block; } } }
  &__logo_blue &__pic {
    &_white {
      display: inline-block; }
    &_black {
      display: none; }
    &_big {
      display: none; } }
  &__search {
    position: relative;
    width: 500px;
    margin-right: 60px;
    @include d {
      width: 400px;
      margin-right: 40px; }
    @include t {
      width: 300px;
      margin-right: 30px; }
    @include m {
      display: none; } }
  &__input {
    width: 100%;
    height: 50px;
    padding: 0 45px 0 15px;
    background: $wh;
    border: 1px solid #F1F1F5;
    border-radius: 15px;
    @include f;
    font-size: 14px;
    color: $dark;
    @include placeholder {
      color: $gray; } }
  &__open {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 44px;
    font-size: 0;
    .icon {
      font-size: 20px;
      fill: $gray;
      transition: fill .25s; }
    &:hover .icon {
      fill: $gray-light; } }
  &__control {
    display: flex;
    align-items: center; }
  &__notifications,
  &__logout {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 24px;
    height: 24px;
    font-size: 0;
    .icon {
      font-size: 22px;
      fill: $gray;
      transition: fill .25s; }
    &:before {
      content: "";
      position: absolute;
      top: -2px;
      right: 1px;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: $red;
      opacity: 0;
      transition: opacity .25s; }
    &:hover .icon {
      fill: $gray-light; }
    &.active {
      &:before {
        opacity: 1; } }
    @include nf {
      margin-left: 30px;
      @include m {
        margin-left: 15px; } } }
  &__user {
    position: relative;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    margin-left: 30px;
    transition: opacity .25s;
    @include m {
      margin-left: 15px; }
    .icon {
      margin-left: 10px;
      font-size: 16px;
      fill: $gray;
      @include m {
        display: none; } }
    &:hover {
      opacity: .8; } }
  &__ava {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: 38px;
    height: 38px;
    border: 3px solid #F1F1F5;
    border-radius: 10px;
    font-weight: 500;
    text-transform: uppercase;
    color: $white; }
  &__ava img {
    width: 32px;
    min-height: 32px;
    border-radius: 10px; }
  &__box {
    padding-left: 10px;
    line-height: 1;
    @include m {
      display: none; } }
  &__man {
    margin-bottom: 3px;
    @include fp;
    font-size: 14px;
    font-weight: 600;
    color: $dark; }
  &__post {
    font-size: 12px;
    color: $gray; }
  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 8;
    background: rgba($dark, .3);
    visibility: hidden;
    opacity: 0;
    transition: all .2s;
    &.visible {
      z-index: 13;
      visibility: visible;
      opacity: 1; } }
  &_empty {
    padding-left: 0;
    @include d {
      padding-left: 25px; }
    @include t {
      padding-left: 20px; } }
  &_empty &__burger {
    @include d {
      margin-right: 10px; }
    @include t {
      margin-right: 20px; }
    @include m {
      margin-right: 15px; } }
  &_sm {
    height: 70px;
    margin-left: 250px;
    @include d {
      margin: 0; } }
  &_sm &__logo {
    display: none;
    @include d {
      display: inline-block;
      margin-right: 50px; }
    @include m {
      margin-right: auto; } }
  &_sm &__search {
    width: 280px;
    margin-right: auto; }
  &_sm &__burger {
    @include d {
      display: inline-block;
      margin-right: 25px; } }
  &_sm &__input {
    height: 38px;
    padding: 0 10px 0 30px;
    border: none;
    background: none; }
  &_sm &__open {
    left: 0;
    right: auto;
    width: 20px;
    .icon {
      font-size: 17px; } } }

@include dark {
  .header4 {
    background: $dark2;
    box-shadow: inset 0px -1px 0px $dark3;
    &__logo_blue {
      background: $dark2; }
    &__pic_black {
      display: none; }
    &__pic_white {
      display: inline-block;
      @include t {
        display: none; } }
    &__pic_black.header4__pic_big {
      @include t {
        display: none; } }
    &__pic_white.header4__pic_big {
      display: none;
      @include t {
        display: inline-block; } }
    &__logo_big .header4__pic_white {
      @include t {
        display: inline-block; } }
    &__bg {
      background: rgba($gray, .3); }
    &__input {
      background: $dark3;
      border-color: $dark3; }
    &__man {
      color: $wh; }
    &__post {
      color: $gray-light; }
    &_sm .header4__input {
      background: none; }
    &_sm .header4__burger {
      @include m {
        margin-right: 20px; } } } }

